	padding: 0;
	margin: 0;
	background: #fff;
	font-family: Roboto, 'Helvetica Neue', arial, sans-serif;
	font-weight: 300;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

#app {
	height: 100%;
}



