@import "ripple.css";
@import "loader.css";


html, body {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
    background: #fff;
    font-family:Circular,-apple-system,BlinkMacSystemFont,Roboto,Helvetica Neue,sans-serif !important;
    font-weight: 300;
    color: #444;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
}
* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

#app {
    height: 100%;
}
ol,ul { margin:0px!important; }

.noscroll {
    overflow: hidden;
}

.sectionTitle {
    margin:20px 0px 10px 0px;
    font:300 1.7rem/2rem Roboto, Arial;
    letter-spacing: -0.2px !important;
    font-family:Circular,-apple-system,BlinkMacSystemFont,Roboto,Helvetica Neue,sans-serif !important;
}

/* Routes Transition CSS */
.navigation-enter-ltr {
    opacity:0.5;
    -webkit-transition-duration: .3s;
         -o-transition-duration: .3s;
            transition-duration: .3s;
    -webkit-transition-property: opacity, -webkit-transform;
    transition-property: opacity, -webkit-transform;
    -o-transition-property: transform, opacity;
    transition-property: transform, opacity;
    transition-property: transform, opacity, -webkit-transform;
    -webkit-transition-timing-function: ease-out;
         -o-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
    -webkit-transform: translate3d(-100%,0,0);
            transform: translate3d(-100%,0,0);
    z-index: 10000;
}
.navigation-enter-rtl {
    opacity:0.5;
    -webkit-transition-duration: .3s;
         -o-transition-duration: .3s;
            transition-duration: .3s;
    -webkit-transition-property: opacity, -webkit-transform;
    transition-property: opacity, -webkit-transform;
    -o-transition-property: transform, opacity;
    transition-property: transform, opacity;
    transition-property: transform, opacity, -webkit-transform;
    -webkit-transition-timing-function: ease-out;
         -o-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
    -webkit-transform: translate3d(100%,0,0);
            transform: translate3d(100%,0,0);
    z-index: 10000;
}

.navigation-enter-active {
    opacity:1;
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
}

.navigation-leave-ltr {
    -webkit-transition-duration: .3s;
         -o-transition-duration: .3s;
            transition-duration: .3s;
    -webkit-transition-property: opacity, -webkit-transform;
    transition-property: opacity, -webkit-transform;
    -o-transition-property: transform, opacity;
    transition-property: transform, opacity;
    transition-property: transform, opacity, -webkit-transform;
    -webkit-transition-timing-function: ease-out;
         -o-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    opacity: 1;
}
.navigation-leave-rtl {
    -webkit-transition-duration: .3s;
         -o-transition-duration: .3s;
            transition-duration: .3s;
    -webkit-transition-property: opacity, -webkit-transform;
    transition-property: opacity, -webkit-transform;
    -o-transition-property: transform, opacity;
    transition-property: transform, opacity;
    transition-property: transform, opacity, -webkit-transform;
    -webkit-transition-timing-function: ease-out;
         -o-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    opacity: 1;
}

.navigation-leave-active {
    -webkit-transform: translate3d(-15%,0,0);
            transform: translate3d(-15%,0,0);
    opacity: 0.3;
}
/* React Tel Input */
.react-tel-input .flag-dropdown {
    padding:5px 0px 0px 0px!important;
}
.react-tel-input input[type=text], .react-tel-input input[type=tel] {
    height: 36px!important;
    width: 100%!important;
    padding-top: 10px!important;
    padding-bottom: 10px!important;
}
.react-tel-input {
    width:auto!important;
}
.Select-menu-outer {
    z-index:100;
}

.container, .container-full {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto; }

.DayPicker-Day--disabled {
    pointer-events:none!important;
}


@media only screen and (min-width: 740px) {
  .container {
    width: 720px; }
  .container-m, .container-full-m {
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto; }
  .container-m {
    width: 720px; }
  .container-full-m {
    width: auto; } }

@media only screen and (min-width: 980px) {
  .container {
    width: 960px; }
  .container-l, .container-full-l {
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto; }
  .container-l {
    width: 960px; }
  .container-full-l {
    width: auto; } }

@media only screen and (min-width: 1140px) {
  .container {
    width: 1120px; }
  .container-xl, .container-full-xl {
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto; }
  .container-xl {
    width: 1120px; }
  .container-full-xl {
    width: auto; } }

