.ripple .wave { 
    display: block; 
    height: 50% !important; 
    width: 50% !important; 
    padding: 0px 0px 0px 0px; 
    border-radius: 50%; 
    position: absolute !important; 
    top: 50%;
    margin-top:-25%;
    left: 50%;
    margin-left:-25%; 
    transform:scale(1); 
    -webkit-transform:scale(1); 
    -ms-transform: scale(1); 
    background-color: #fff;
    opacity:0;
    pointer-events: none !important;
    z-index:99;
    
}
.ripple { 
    text-decoration:none; 
    -webkit-touch-callout: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    cursor:default;
    
}
.ripple * { 
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    pointer-events:none!important;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.ripple.animate .wave {
    -webkit-animation: rippling 0.2s 1;
            animation: rippling 0.2s 1;
}
@-webkit-keyframes rippling {
    40% {
        background: -webkit-radial-gradient( center circle, white 30%, transparent 32%);
        background: radial-gradient( circle at center, white 30%, transparent 32%);
        -webkit-transform:scale(6);
                transform:scale(6);
        opacity:0.3;
    }
    100% {
        -webkit-transform:scale(20);
                transform:scale(20);
        opacity:0;
    }
}
@keyframes rippling {
    40% {
        background: -webkit-radial-gradient( center circle, white 30%, transparent 32%);
        background: -o-radial-gradient( center circle, white 30%, transparent 32%);
        background: radial-gradient( circle at center, white 30%, transparent 32%);
        -webkit-transform:scale(6);
                transform:scale(6);
        opacity:0.3;
    }
    100% {
        -webkit-transform:scale(20);
                transform:scale(20);
        opacity:0;
    }
}



